<template>
  <div class="container-fluid p-5 d-flex">
    <div class="board w-50 py-5">
      <div class="tic-row d-flex">
        <div id="0" class="tic-col" @click="play">
          <h1>{{ cellData[0].player }}</h1>
        </div>
        <div
          id="1"
          class="tic-col border-start border-end border-dark border-3"
          @click="play"
        >
          <h1>{{ cellData[1].player }}</h1>
        </div>
        <div id="2" class="tic-col" @click="play">
          <h1>{{ cellData[2].player }}</h1>
        </div>
      </div>
      <div class="tic-row d-flex">
        <div
          id="3"
          class="tic-col border-top border-bottom border-dark border-3"
          @click="play"
        >
          <h1>{{ cellData[3].player }}</h1>
        </div>
        <div id="4" class="tic-col border border-dark border-3" @click="play">
          <h1>{{ cellData[4].player }}</h1>
        </div>
        <div
          id="5"
          class="tic-col border-top border-bottom border-dark border-3"
          @click="play"
        >
          <h1>{{ cellData[5].player }}</h1>
        </div>
      </div>
      <div class="tic-row d-flex">
        <div id="6" class="tic-col" @click="play">
          <h1>{{ cellData[6].player }}</h1>
        </div>
        <div
          id="7"
          class="tic-col border-start border-end border-dark border-3"
          @click="play"
        >
          <h1>{{ cellData[7].player }}</h1>
        </div>
        <div id="8" class="tic-col" @click="play">
          <h1>{{ cellData[8].player }}</h1>
        </div>
      </div>
    </div>
    <div class="state w-50 px-3 py-5">
      <h1 class="mb-4" style="font-weight: 900">Tic Tac Toe</h1>
      <h4 class="mb-5" v-if="judged == false">
        {{ turn % 2 == 0 ? "⭕ 차례" : "❌ 차례" }}
      </h4>
      <h4 class="mb-5" v-if="judged == true">
        {{ "경기가 끝났습니다." }}
      </h4>
      <h4
        v-if="judged == true"
        class="border border-5 rounded-3 border-dark p-4 w-50 mx-auto"
      >
        {{ result }}
      </h4>
      <button class="mt-3 p-2 rounded-3" @click="clear">
        <h3 class="m-0">새경기</h3>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: "App",
  components: {},
  data() {
    return {
      turn: 0,
      cellData: [
        { cell: 0, player: "" },
        { cell: 1, player: "" },
        { cell: 2, player: "" },
        { cell: 3, player: "" },
        { cell: 4, player: "" },
        { cell: 5, player: "" },
        { cell: 6, player: "" },
        { cell: 7, player: "" },
        { cell: 8, player: "" },
      ],
      result: "",
      judged: false,
    };
  },
  methods: {
    play(e) {
      let i = e.target.id;
      if (this.judged == false) {
        if (this.turn % 2 == 0) {
          this.cellData[i].player = "⭕";
          this.turn++;
        } else {
          this.cellData[i].player = "❌";
          this.turn++;
        }
      }
    },
    judge() {
      if (
        this.cellData[0].player == "⭕" &&
        this.cellData[1].player == "⭕" &&
        this.cellData[2].player == "⭕"
      ) {
        this.result = " '⭕' 승리 ";
        this.judged = true;
        return;
      } else if (
        this.cellData[0].player == "⭕" &&
        this.cellData[3].player == "⭕" &&
        this.cellData[6].player == "⭕"
      ) {
        this.result = " '⭕' 승리 ";
        this.judged = true;
        return;
      } else if (
        this.cellData[0].player == "⭕" &&
        this.cellData[4].player == "⭕" &&
        this.cellData[8].player == "⭕"
      ) {
        this.result = " '⭕' 승리 ";
        this.judged = true;
        return;
      } else if (
        this.cellData[1].player == "⭕" &&
        this.cellData[4].player == "⭕" &&
        this.cellData[7].player == "⭕"
      ) {
        this.result = " '⭕' 승리 ";
        this.judged = true;
        return;
      } else if (
        this.cellData[2].player == "⭕" &&
        this.cellData[4].player == "⭕" &&
        this.cellData[6].player == "⭕"
      ) {
        this.result = " '⭕' 승리 ";
        this.judged = true;
        return;
      } else if (
        this.cellData[2].player == "⭕" &&
        this.cellData[5].player == "⭕" &&
        this.cellData[8].player == "⭕"
      ) {
        this.result = " '⭕' 승리 ";
        this.judged = true;
        return;
      } else if (
        this.cellData[3].player == "⭕" &&
        this.cellData[4].player == "⭕" &&
        this.cellData[5].player == "⭕"
      ) {
        this.result = " '⭕' 승리 ";
        this.judged = true;
        return;
      } else if (
        this.cellData[6].player == "⭕" &&
        this.cellData[7].player == "⭕" &&
        this.cellData[8].player == "⭕"
      ) {
        this.result = " '⭕' 승리 ";
        this.judged = true;
        return;
      } else if (
        this.cellData[0].player == "❌" &&
        this.cellData[1].player == "❌" &&
        this.cellData[2].player == "❌"
      ) {
        this.result = " '❌' 승리 ";
        this.judged = true;
        return;
      } else if (
        this.cellData[0].player == "❌" &&
        this.cellData[3].player == "❌" &&
        this.cellData[6].player == "❌"
      ) {
        this.result = " '❌' 승리 ";
        this.judged = true;
        return;
      } else if (
        this.cellData[0].player == "❌" &&
        this.cellData[4].player == "❌" &&
        this.cellData[8].player == "❌"
      ) {
        this.result = " '❌' 승리 ";
        this.judged = true;
        return;
      } else if (
        this.cellData[1].player == "❌" &&
        this.cellData[4].player == "❌" &&
        this.cellData[7].player == "❌"
      ) {
        this.result = " '❌' 승리 ";
        this.judged = true;
        return;
      } else if (
        this.cellData[2].player == "❌" &&
        this.cellData[4].player == "❌" &&
        this.cellData[6].player == "❌"
      ) {
        this.result = " '❌' 승리 ";
        this.judged = true;
        return;
      } else if (
        this.cellData[2].player == "❌" &&
        this.cellData[5].player == "❌" &&
        this.cellData[8].player == "❌"
      ) {
        this.result = " '❌' 승리 ";
        this.judged = true;
        return;
      } else if (
        this.cellData[3].player == "❌" &&
        this.cellData[4].player == "❌" &&
        this.cellData[5].player == "❌"
      ) {
        this.result = " '❌' 승리 ";
        this.judged = true;
        return;
      } else if (
        this.cellData[6].player == "❌" &&
        this.cellData[7].player == "❌" &&
        this.cellData[8].player == "❌"
      ) {
        this.result = " '❌' 승리 ";
        this.judged = true;
        return;
      } else if (this.turns == 9) {
        this.result = " 무승부 ";
        this.judged = true;
      }
    },
    clear() {
      this.turn = 0;
      this.cellData = [
        { cell: 0, player: "" },
        { cell: 1, player: "" },
        { cell: 2, player: "" },
        { cell: 3, player: "" },
        { cell: 4, player: "" },
        { cell: 5, player: "" },
        { cell: 6, player: "" },
        { cell: 7, player: "" },
        { cell: 8, player: "" },
      ];
      this.result = "";
      this.judged = false;
    },
  },
  updated() {
    this.judge();
  },
};
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  box-sizing: border-box;
}
.container-fluid {
  background-color: rgba(250, 213, 190, 0.589);
}
.tic-row {
  width: 300px;
  height: 100px;
}
.tic-col {
  width: 100px;
  height: 100px;
  padding: 11px 0;
}
.tic-col h1 {
  font-size: 60px;
  margin: 0;
  text-align: center;
}
</style>
